import axios from "@/plugins/axios";

import endpoints from "@/data/endpoints";

export default {
    async getGoogleRedirect({ commit }) {
        let url = `${endpoints.auth.google}/?redirect_uri=${process.env.VUE_APP_API_URL}/rest/v1/redirect/`;

        let data = await axios.get(url);

        commit("setGoogleRedirect", data);
    },

    async googleLogin({ commit, dispatch }, { code, state }) {
        if (!state || state == "") return;

        try {
            let data = await axios.post(`${endpoints.auth.google}/?code=${code}&state=${state}`);

            commit("setGoogleAuth", data);

            await dispatch('getMe')
        } catch (error) {
            console.log(error);
        }
    },

    async getZoomRedirect({ commit }) {
        let url = `${endpoints.auth.zoomRedirect}/?redirect_uri=${process.env.VUE_APP_API_URL}/zoom/redirect/`;

        let data = await axios.get(url);

        commit("setZoomRedirect", data);
    },

    async zoom({ dispatch }, { code, state }) {
        if (!state || state == "") return;

        try {
            await axios.post(`${endpoints.auth.me.zoom}/?code=${code}&state=${state}`);

            await dispatch("getMe");

        } catch (error) {
            console.log(error);
        }
    },

    async zoomDisconnect({ dispatch }) {
        try {
            await axios.delete(`${endpoints.auth.me.zoom}/`);

            await dispatch("getMe");

        } catch (error) {
            console.log(error);
        }
    },

    async getMe({ commit }) {
        try {
            let data = await axios.get(`${endpoints.auth.me.base}`);

            commit("setMe", data);
        } catch (error) {
            commit("setMe", {});
        }
    },

    logout({ commit }) {
        commit("logout")
    },

    async updateProfile({ dispatch }, data) {
        try {
            if (!Array.isArray(data)) {
                data = [data];
            }

            await axios.put(`${endpoints.auth.me.profile}/`, ...data);

            await dispatch("getMe");
        } catch (error) {
            console.log(error);
            alert("Something went wrong!");
        }
    },

    async updateMe({ commit }, payload) {
        try {
            let data = await axios.patch(`${endpoints.auth.me.base}/`, payload);

            commit("setMe", data);
        } catch (error) {
            console.log(error);
            alert("Something went wrong!");
        }
    },

    async getCalendars({ commit }, payload) {
        try {
            let response = await axios.get(`${endpoints.auth.me.calendars}/`, payload);

            commit("setCalendars", response.calendars);
        } catch (error) {
            console.log(error);
            alert("Something went wrong!");
        }
    },

    getEvent({ commit, state }, id) {
        let event = state.events.all.find((event) => event.id == id);

        commit("setEvent", event);
    },
}